<template>
	<div style="background-color:#f0ebe7; height: 50rem;">

      <div v-if="!state.room" style="padding-top:5rem">
        <div>
          <h3 style="padding-top:5rem; display:inline; " class="waitingguest-font">, </h3>
          <h2 style="display:inline" class="waitingguest-font"> {{ state.guestInfo.guestName }}({{ state.guestInfo.guestGroup }})</h2>
          <h3 style="display:inline" class="waitingguest-font">  </h3>
        </div>
        <div style="margin-top:3rem">
          <h3 class="waitingguest-font" style="display:inline">   </h3>
        </div>
        <div style="margin-top:3rem">
          <h3 class="waitingguest-font">   </h3>
        </div>
        <div class="text-center" style="margin-top:3rem">
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('1')">  1</button>
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('2')">  2</button>
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('3')">  3</button>
        </div>
         <div class="text-center" style="margin-top:1rem">
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('4')">  1</button>
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('5')">  2</button>
          <button style="margin-left:1rem" type="button" class="btn btn-outline-secondary" @click="clickButton('6')">  3</button>
        </div>
        <div style="margin-top:3rem">
          <button type="button" class="btn btn-danger" @click="clickExit"></button>
        </div>
        <div style="width:40rem; margin:auto; margin-top:3rem" id="carouselExampleFade" class="carousel slide carousel-fade" data-bs-ride="carousel">
          <div class="carousel-inner">
            <div class="carousel-item active">
            <img src="@/assets/wedding_main_1.jpg" class="d-block w-100" alt="...">
            </div>
            <div class="carousel-item">
            <img src="@/assets/wedding_main_2.jpg" class="d-block w-100" alt="...">
            </div>
            <div class="carousel-item">
            <img src="@/assets/wedding_main_3.jpg" class="d-block w-100" alt="...">
            </div>
          </div>
          <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Previous</span>
          </button>
          <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Next</span>
          </button>
        </div>
      </div>

      <guest-to-guest-video
        v-if="state.room"
        :room="state.room"
        @leaveGuestRoom="leaveGuestRoom"
      ></guest-to-guest-video>
    </div>
</template>

<script>
import { computed, reactive } from 'vue'
import { useStore } from 'vuex'
import GuestToGuestVideo from '@/components/ConferenceGuest/GuestToGuestVideo'

export default {
  components: {
    GuestToGuestVideo,
  },
  setup(props, { emit }) {
    const store = useStore()
    const state = reactive({
      room: false,
      guestInfo: computed(() => store.getters['root/getNowGuestInfo'])
    })

    const clickButton = function(target) {
      state.room = target
    }

    const leaveGuestRoom = function() {
      state.room = false
    }

    const clickExit = function() {
      emit('click-exit')
    }

    return { state, clickButton, leaveGuestRoom, clickExit }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap');

	.waitingguest-font{
		font-family: 'Gowun Dodum', sans-serif;
	}
</style>
