<template>
  <div class="hallvideoguest">
    <div id="session" v-if="session">
      <h3 class="hallvideoguest-font" style="padding-top:5rem" id="session-title"> {{ wedding.weddingManNm }},  {{wedding.weddingWomanNm}} .</h3>
      <div class="container" style="margin-top:2rem">
        <div id="video-container" class=" user-list d-flex align-items-start flex-wrap:nowrap " style="width:1272px;">
          <user-video class="small-video" :stream-manager="mainStreamManager" />
          <div class="other-list" style="margin-left:2rem;">
            <user-video style="margin-right:2rem;"
              class="small-other-video"
              v-for="sub in subscribers"
              :key="sub.stream.connection.connectionId"
              :stream-manager="sub"
              @click.native="updateMainVideoStreamManager(sub)"
            />
          </div>
        </div>
      </div>
      <div id="session-header"  style="margin-top:2rem">
        <video-on-off
          ref="video-on-off"
          @videoOnOff="videoOnOff"
        ></video-on-off>
        <input
          class="btn btn-large btn-danger btnExit"
          type="button"
          id="buttonLeaveSession"
          @click="leaveSession"
          value=""
        />
      </div>
      <div class="d-flex">
        <div id="main-video" class="d-flex justify-content-center" style="width: 1000px;height: 600px;margin-left: auto;margin-right: auto;">
          <user-video class="wedding-video" :stream-manager="maincompany"/>
        </div>
        <room-chat
        class="chatDesign"
        v-show="this.disabled"
        ref="chat"
        @message="sendMessage"
        :subscribers="subscribers"
        ></room-chat>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { OpenVidu } from "openvidu-browser";
import UserVideo from "@/components/UserVideo";
import { mapGetters } from "vuex";
import VideoOnOff from "@/components/VideoOnOff"
import AudioOnOff from "@/components/AudioOnOff"
import RoomChat from "@/components/ConferenceGuest/ChatForHallGuest";
axios.defaults.headers.post["Content-Type"] = "application/json";

const OPENVIDU_SERVER_URL = "https://" + location.hostname + ':' + process.env.VUE_APP_OPENVIDU_PORT;
const OPENVIDU_SERVER_SECRET = "MY_SECRET";

export default {
  name: "hall-guest",

  props: {
    where: String,
  },

  components: {
    UserVideo,
    VideoOnOff,
    AudioOnOff,
    RoomChat,
  },

  data() {
    return {
      OV: undefined,
      session: undefined,
      mainStreamManager: undefined,
      publisher: undefined,
      subscribers: [],
      maincompany: undefined,
      is_maincompany: false,
      disabled: true,
      mySessionId: "",
      myUserName: "",
    };
  },

  methods: {
    // outFirstMan (where) {
    // 	this.$emit('out-first-man', where)
    // },

    joinSession(target) {
      this.mySessionId = target;
      this.myUserName = target + this.guest.guestGroup + ' '+ this.guest.guestName;
      // --- Get an OpenVidu object ---
      this.OV = new OpenVidu();

      // --- Init a session ---
      this.session = this.OV.initSession();
      // --- Specify the actions when events take place in the session ---

      // On every new Stream received...
      this.session.on("streamCreated", ({ stream }) => {
        const subscriber = this.session.subscribe(stream);
        this.subscribers.push(subscriber);
        if(!this.is_maincompany){
          this.subscribers.forEach((element) => {
            if (element.stream.connection.data.includes('Company')) {
              this.maincompany = element
              this.is_maincompany = true
              const idx = this.subscribers.indexOf(element)
              this.subscribers.splice(idx,1)
            }
            // if ((element.stream.connection.data.slice(15, -2) === this.mySessionId,"Company")) {
            //   this.maincompany = element
            //   this.is_maincompany = true
            // }
          });
        }
      });

      // On every Stream destroyed...
      this.session.on("streamDestroyed", ({ stream }) => {
        const index = this.subscribers.indexOf(stream.streamManager, 0);
        if (index >= 0) {
          this.subscribers.splice(index, 1);
        }
      });

      // On every asynchronous exception...
      this.session.on("exception", ({ exception }) => {
        console.warn(exception);
      });

      // public  signal 
      this.session.on("signal:public-chat", (event) => {
        this.$refs.chat.addMessage(
          event.data,
          JSON.parse(event.data).sender === this.myUserName,
          false
        );
      });

      // private  signal 
      this.session.on("signal:private-chat", (event) => {
        this.$refs.chat.addMessage(event.data, false, true);
      });

      // --- Connect to the session with a valid user token ---

      // 'getToken' method is simulating what your server-side should do.
      // 'token' parameter should be retrieved and returned by your own backend
      this.getToken(target + this.mySessionId).then((token) => {
        this.session
          .connect(token, { clientData: this.myUserName })
          .then(() => {
            // --- Get your own camera stream with the desired properties ---
            // let publisher = this.maincompany(undefined, {
            let publisher = this.OV.initPublisher(undefined, {
              audioSource: undefined, // The source of audio. If undefined default microphone
              videoSource: undefined, // The source of video. If undefined default webcam
              publishAudio: false, // Whether you want to start publishing with your audio unmuted or not
              publishVideo: true, // Whether you want to start publishing with your video enabled or not
              resolution: "640x480", // The resolution of your video
              // resolution: "1280x480", // The resolution of your video
              // resolution: '1290x960',  // The resolution of your video
              frameRate: 30, // The frame rate of your video
              insertMode: "APPEND", // How the video is inserted in the target element 'video-container'
              mirror: false, // Whether to mirror your local video or not
            });

            this.mainStreamManager = publisher;
            this.publisher = publisher;

            // --- Publish your stream ---

            this.session.publish(this.publisher);
          })
          .catch((error) => {
            console.log(
              "There was an error connecting to the session:",
              error.code,
              error.message
            );
          });
      });

      window.addEventListener("beforeunload", this.leaveSession);
    },

    leaveSession() {
      // --- Leave the session by calling 'disconnect' method over the Session object ---
      if (this.session) this.session.disconnect();

      this.session = undefined;
      this.mainStreamManager = undefined;
      this.publisher = undefined;
      this.subscribers = [];
      this.OV = undefined;

      this.$emit("leave-video");
      window.removeEventListener("beforeunload", this.leaveSession);
    },

    updateMainVideoStreamManager(stream) {
      if (this.mainStreamManager === stream) return;
      this.mainStreamManager = stream;
    },

    /**
     * --------------------------
     * SERVER-SIDE RESPONSIBILITY
     * --------------------------
     * These methods retrieve the mandatory user token from OpenVidu Server.
     * This behavior MUST BE IN YOUR SERVER-SIDE IN PRODUCTION (by using
     * the API REST, openvidu-java-client or openvidu-node-client):
     *   1) Initialize a Session in OpenVidu Server	(POST /openvidu/api/sessions)
     *   2) Create a Connection in OpenVidu Server (POST /openvidu/api/sessions/<SESSION_ID>/connection)
     *   3) The Connection.token must be consumed in Session.connect() method
     */

    getToken(mySessionId) {
      return this.createSession(mySessionId).then((sessionId) =>
        this.createToken(sessionId)
      );
    },

    // See https://docs.openvidu.io/en/stable/reference-docs/REST-API/#post-session
    createSession(sessionId) {
      return new Promise((resolve, reject) => {
        axios
          .post(
            `${OPENVIDU_SERVER_URL}/openvidu/api/sessions`,
            JSON.stringify({
              customSessionId: sessionId,
            }),
            {
              auth: {
                username: "OPENVIDUAPP",
                password: OPENVIDU_SERVER_SECRET,
              },
            }
          )
          .then((response) => response.data)
          .then((data) => resolve(data.id))
          .catch((error) => {
            if (error.response.status === 409) {
              resolve(sessionId);
            } else {
              console.warn(
                `No connection to OpenVidu Server. This may be a certificate error at ${OPENVIDU_SERVER_URL}`
              );
              if (
                window.confirm(
                  `No connection to OpenVidu Server. This may be a certificate error at ${OPENVIDU_SERVER_URL}\n\nClick OK to navigate and accept it. If no certificate warning is shown, then check that your OpenVidu Server is up and running at "${OPENVIDU_SERVER_URL}"`
                )
              ) {
                location.assign(`${OPENVIDU_SERVER_URL}/accept-certificate`);
              }
              reject(error.response);
            }
          });
      });
    },

    // See https://docs.openvidu.io/en/stable/reference-docs/REST-API/#post-connection
    createToken(sessionId) {
      return new Promise((resolve, reject) => {
        axios
          .post(
            `${OPENVIDU_SERVER_URL}/openvidu/api/sessions/${sessionId}/connection`,
            {},
            {
              auth: {
                username: "OPENVIDUAPP",
                password: OPENVIDU_SERVER_SECRET,
              },
            }
          )
          .then((response) => response.data)
          .then((data) => resolve(data.token))
          .catch((error) => reject(error.response));
      });
    },

    sendMessage({ content, to }) {
      let now = new Date();
      let current = now.toLocaleTimeString([], {
        hour: "2-digit",
        minute: "2-digit",
        hour12: false, // true   10:25  .
      });

      let messageData = {
        content: content,
        sender: this.myUserName,
        time: current,
      };

      //  
      if (to === "all") {
        this.session
          .signal({
            data: JSON.stringify(messageData),
            to: [],
            type: "public-chat",
          })
          .then(() => {
          })
          .catch((error) => {
            console.log(error);
          });
      }

      //  
      if (to !== "all") {
        this.session
          .signal({
            data: JSON.stringify(messageData),
            to: [to],
            type: "private-chat",
          })
          .then(() => {
            //     
            this.$refs.chat.addMessage(JSON.stringify(messageData), true, true);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    audioOnOff({ audio }) {
      this.publisher.publishAudio(audio);
    },

    videoOnOff({ video }) {
      this.publisher.publishVideo(video);
    },
  },
  computed: mapGetters({
    wedding: "root/getNowWeddingInfo",
    guest: "root/getNowGuestInfo",
  }),

  mounted() {
    this.joinSession(this.wedding.weddingNum + this.where);
  },
  beforeUnmount() {
    this.leaveSession();
  },
};
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap');

.hallvideoguest-font{
font-family: 'Gowun Dodum', sans-serif;
}

.small-video {
  width: 200px;
}
.small-other-video {
  width: 200px;
  display:inline-block;
}
.main-video .main-video video {
	width: 50%;
}

video {
  height: 100%;
  width: 100%;
}
.other-list {
  white-space: nowrap;
  overflow-x: scroll;
  max-width: 63.5rem;
}
#video-container {
  max-width: 79.5rem;
}
#session-header {
  margin-bottom: 2rem;
}

.btnExit{
  font-size: 20px;
}
::-webkit-scrollbar {
  display: none;
}
